\n    //overflow-x: hidden;\n    line-height: $font-lheight-default / $font-size-default;\n}\n\n//Remove text-shadow in selection highlight: h5bp.com/i\n//These selection declarations have to be separate.\n//Customize the background color to match your design.\n\n::-moz-selection {\n    background-color: rgba($color-link, 0.2);\n    color: darken($color-link, 15%);\n    text-shadow: none;\n}\n\n::selection {\n    background-color: rgba($color-link, 0.2);\n    color: darken($color-link, 15%);\n    text-shadow: none;\n}\n\n/*\n::-webkit-scrollbar {\n    width: 8px;\n}\n\n::-webkit-scrollbar-thumb {\n    background: darken($color-link, 10%);\n}\n\n::-webkit-scrollbar-track {\n    background: $color-link;\n}\n*/\n// Remove the gap between images and the bottom of their containers: h5bp.com/i/440\n\naudio,\ncanvas,\niframe,\nsvg,\nvideo,\nimg {\n    vertical-align: middle;\n}\n\n// A better looking default horizontal rule\n\nhr {\n    display: block;\n    height: 1px;\n    margin: 1rem 0;\n    padding: 0;\n    border: 0;\n    border-top: 1px solid $color-border;\n}\n\n// Remove default fieldset styles.\n\nfieldset {\n    margin: 0;\n    padding: 0;\n    border: 0;\n}\n\n// Allow only vertical resizing of textareas.\n\ntextarea {\n    resize: vertical;\n}\n\ninput::-moz-focus-inner {\n    border: 0;\n}\n\n\n/* ==========================================================================\n   ##prevent 300ms delay in FF, Chrome and IE on touch devices\n   ========================================================================== */\n\na[href],\nbutton {\n    touch-action: manipulation;\n}\n\n/* ==========================================================================\n   ##Headings and Base\n\n   1 to 6 heading\n\n   ```\n   ```\n   ========================================================================== */\n\n@each $font-head-num, $font-head-size in $font-sizes-headings {\n\n    h#{$font-head-num},\n    :global(.h#{$font-head-num}) {\n        margin: 0 0 0.2em;\n        //color: $color-text\n        font-family: $font-family-h;\n        font-size: font-size($font-head-size);\n        font-weight: font-weight(regular);\n        line-height: 1.2;\n    }\n}\n\np {\n    margin: 0 0 0.2em;\n    line-height: 1.2;\n}\n\nabbr[title] {\n    border-bottom: 0;\n\n    :global(.no-touchevents) & {\n        border-bottom: 1px dotted currentColor;\n        text-decoration: none;\n\n        @include mq($media-type: print) {\n            border-bottom: 0;\n        }\n    }\n}\n\n:global(a.u-link--mark),\n:global(a:not([class])) {\n    background-image:\n        linear-gradient(\n            to top,\n            rgba($color-text, 0.2) 0,\n            rgba($color-text, 0.2) 3px,\n            transparent 4px\n        );\n    color: inherit;\n    outline: 0;\n    text-decoration: none;\n}\n\n:global(.no-touchevents a.u-link--mark),\n:global(.no-touchevents a:not([class])) {\n    position: relative;\n    display: inline-block;\n    padding-right: 0.22em;\n    padding-left: 0.22em;\n    background-image: none;\n    color: inherit;\n    outline: 0;\n    text-decoration: none;\n    transition: all 0.27s;\n\n\n    &::after {\n        position: absolute;\n        right: 0;\n        bottom: -0.17em;\n        left: 0;\n        z-index: z-index(behind) - 1;\n        opacity: 0.1;\n        height: 3px;\n        background-color: currentColor;\n        content: \"\";\n        pointer-events: none;\n        transition: all 0.1s linear;\n    }\n\n    &::before {\n        position: absolute;\n        top: 0;\n        bottom: -0.17em;\n        left: 0;\n        z-index: z-index(above);\n        opacity: 0;\n        width: 0;\n        background-color: $color-link;\n        content: \"\";\n        pointer-events: none;\n        transform: scaleX(1);\n        transform-origin: 0 0;\n        transition: opacity 0.25s, width 0s ease 0.25s, transform 0s ease 0.25s, transform-origin 0s ease 0.25s;\n    }\n\n    &:hover {\n        color: $color-link;\n        text-decoration: none;\n        transition-delay: 0.25s;\n        transition-duration: 0;\n\n        &::before {\n            opacity: 1;\n            width: 100%;\n            transform: scaleX(0);\n            transform-origin: right 0;\n            transition: width 0.25s ease, transform-origin 0s ease 0.25s, transform 0.25s ease 0.25s;\n        }\n\n        &::after {\n            opacity: 0.3;\n            transition: opacity 0.15s ease 0.15s;\n        }\n    }\n}\n\n[data-whatinput=\"keyboard\"] a:not([class]) {\n    @include a11y-anchor-focus;\n}\n\n/* ==========================================================================\n   ## Layout settings\n   ========================================================================== */\n:global {\n\n    [data-scrollbar] .scrollbar-track {\n        background: $color-link !important;\n\n        @media print {\n            display: none !important;\n        }\n    }\n\n    [data-scrollbar] .scrollbar-track-y {\n        opacity: 1 !important;\n    }\n\n    [data-scrollbar] .scrollbar-track-x {\n        display: none !important;\n    }\n\n    [data-scrollbar] .scrollbar-thumb {\n        background: darken($color-link, 10%) !important;\n    }\n}","/* ==========================================================================\n\n   Base Styles\n   ====\n\n\n   Some Basic Styles\n   ========================================================================== */\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n  -webkit-tap-highlight-color: transparent;\n}\n\nhtml,\nbutton,\ninput,\nselect,\ntextarea {\n  color: #4d4d4d;\n}\n\nhtml {\n  /*\n    $font-min: $font-size-default;\n    //$font-min: 14px;\n    //$font-max: 18px;\n    $font-max: 20px;\n    $font-diff: ($font-max - $font-min);\n    //$breakpoint-min: 1024px;\n    $breakpoint-min: 1280px;\n    $breakpoint-max: 2560px;\n    //$breakpoint-max: 1920px;\n    $breakpoint-diff: ($breakpoint-max - $breakpoint-min);\n    $font-scale: $font-diff / $breakpoint-diff;\n    */\n  font-size